<template>
  <h1>{{ message }}</h1>
    Tytuł<br />
    <input id="title" type="text" v-model="title" /><br />
    Autor<br />
    <input id="author" type="text" v-model="author" /><br />
    Ilość stron<br />
    <input id="pages" type="number" v-model="pages" /><br /> 
    <button v-on:click="addBook()">Dodaj</button>
    <Child @custom-event-name="setMessage" msg =""/>
    <table class="table table-striped table-bordered">
            <thead>
                <tr>
                    <th>Tytuł</th>
                    <th>Autor</th>
                    <th>Ilość stron</th>
                    <th>Opcje</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="book in books" :key="book.id">
                    <td>{{book.title}}</td>
                    <td>{{book.author}}</td>
                    <td>{{book.pages}}</td>
                    <td><button type="button" class="btn btn-danger" v-on:click="deleteBook(book.title)">Usuń</button></td>
                </tr>
            </tbody>
    </table>
  
</template>

<script>
import Child from "./ListBooks.vue";

export default {
  name: "App",
  components: { Child },
  data() {
    return {
      message: "",
      books:[{
      title:"Balladyna",
      author:"Juliusz Słowacki",
      pages:"100"
      },
      {
      title:"Iliada",
      author:"Homer",
      pages:"111"
      },
      {
      title:"O psie, który jeździł koleją",
      author:"Roman Pisarski",
      pages:"64"
      }]   
    };
  },
  methods: {
    setMessage() {
      this.message = this.books
    },
    addBook(){
      this.books.push({
        title:this.title,
        author:this.author,
        pages:this.pages
      })
      console.log(this.books)
    },
    deleteBook(title){
      this.books = this.books.filter((books) => books.title !== title);
    }
  },
};
</script>
